.main-slider-wrapper {
    
}


.hero-image-slider {
    width: 100%;
    height: 400px;
    overflow: hidden;
    object-fit: cover;
}
.hero-image-slider img {
    width: 100%;
}
.slick-prev {
    left: 22px !important;
    z-index: 1;
    opacity: 0 !important;
}
.slick-next {
    right: 22px !important;
    z-index: 1;
    opacity: 0 !important;
}
