.checkoutProduct {
    padding: 10px;
    width: 100%;
}

.checkoutProduct_wrap {
    width: 100% !important;
    /* min-width: 35rem; */
    height: auto;
    display: flex;
    padding: 1.5rem ;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    align-items: center;
    overflow: hidden;
    border-radius: 3px;
}
.checkoutProduct_info {
    width: 100%;
}


.checkoutProduct_remove {
    display: flex;
    cursor: pointer;
    background: transparent;
    align-items: center;
    border: none;
    color: black;
    font-size: 25px;
}

.checkoutProduct_image {
    width: 200px;
    height: 190px;
    margin-right: 16px;
    display: block;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}
.checkoutProduct_title{
    width: 80%;
    font-size: 20px;
    color: var(--primary-color);
}

.checkoutProduct_rating {
    display: flex;
    margin-top: 10px;
}
.checkoutProduct_price {
    display: flex;
    font-size: 14px;
    align-items: center;
}

.checkoutProduct_info_div1, .checkoutProduct_info_div2 {
    font-size: 13px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    align-items: center
    ;
}
.checkoutProduct_info_div3{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding-right: 0;
    margin: 0;
    right: 0;
}


.quantity_box {
    align-items: center;
    border-radius: 3px;
    display: inline-flex;
    overflow: hidden;
    -webkit-user-select: none;
    user-select: none;
}
.quantity_box button {
    background-color: var(--primary-color);
    color: white;
    border-radius: 3px;
    height: 2.0rem;
    transition: color .2s ease;
    width: 3.10rem;
    cursor: pointer;
    margin: 0;
}
.quantity_box > button > svg {

    color: white;
    -webkit-user-select: none;
    user-select: none;
    vertical-align: middle;
}
.quantity_count {
    font-size: 1.4rem;
    font-weight: 700;
    height: 2.25rem;
    line-height: 2.25rem;
    text-align: center;
    width: 3.75rem;
    color: var(--primary-color);
}
.quantity_box > button {
    border: none;
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    line-height: 1;
    outline: none;
}

@media (max-width: 480px) {
    .checkoutProduct_wrap {
        padding: 0.2rem 1rem;
    }
    .checkoutProduct_title {
        font-size: 16px;
    }
    .checkoutProduct_info_div2 {
        flex-direction: column;
        align-items: end;
    }
    .checkoutProduct_price {
        font-size: 14px;
    }
    .checkoutProduct_info_div1, .checkoutProduct_info_div2 {
        margin-bottom: 8px;
    }
    .quantity_box button  {
        height: 1.5rem;
    }
}